  .modal-content{
    border: none;
  }    
  .modal-header{
    border: none;
    justify-content: center!important;
  }
 
 .modal-header .close{
   padding: 0px;
   margin: 0px;
   position: absolute;
   right: 17px;
   top: 26px;
   background-color:  #9331DC;
   border-radius: 50%;
   opacity: 1;
   width: 26.67px;
   height: 26.67px;
   text-shadow: none;
 }
 .modal-header{
   background-color: #000000;
   padding: 23px 0px;
   h5{
     font-family: Lato;
     font-style: normal;
     font-weight: bold;
     font-size: 28px;
     line-height: 34px;
     color: #ffffff;
   }
